import React, {
	Component,
	PropTypes
} from 'react'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group'

const MessageLists = (props) => {
	// flag
	const getBgColor = (flag) => {
		// console.log(flag)
		switch (flag) {
			case "hint":
				return "bg-warn shadow"
			case "success":
				return "bg-success shadow"
			case "warning":
				return "bg-fail shadow"
			default:
				return "bg-fail shadow"
		}
	}
	const handleMapStatus = (status = 'success') => {
		switch (status) {
			case 'success':
				return 'c-success';
			case 'hint':
				return 'c-warn';
			case 'warning':
				return 'c-fail';
			default:
				return 'c-success';
		}
	}

	const handleMapTitle = (status = 'success') => {
		switch (status) {
			case 'success':
				return '';
			case 'hint':
				return '';
			case 'warning':
				return '';
			default:
				return false
		}
	}
	return (
		<div className="toastlists-normalize-box">
			{
				props.lists.map((item, index) => {
					return (
						<ReactCSSTransitionGroup
							transitionName="example"
							transitionAppear={true}
							transitionAppearTimeout={500}
							transitionEnter={false}
							transitionLeave={false}
							key={index}>
							{/* <div className="toastlist" key={index}>
								<div className={(() => getBgColor(elem.flag))()}>{elem.info}</div>
							</div> */}
							<div className="toastlist" key={index}>
								<div className="toast-item paper" >
									<div className={['pb8', handleMapStatus(item.flag)].join(' ')} style={{ minWidth: 256 + 'px' }}>{handleMapTitle(item.flag) || ''}</div>
									<div className="">{item.info}</div>
								</div>
							</div>
						</ReactCSSTransitionGroup>
					)
				})
			}

		</div>
	)
}

MessageLists.defaultProps = {
	lists: [{
		info: "Msg Loading"
	}]
}

export default MessageLists
